
import Badge from 'vux/src/components/badge/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XSwitch from 'vux/src/components/x-switch/index.vue'

import { mapGetters, mapState } from 'vuex'
export default {
name: 'content',
data () {
return {
}
},
components: {
Badge, XTable, LoadMore, Popup, Group, Cell, XButton, TransferDom, XSwitch
},
computed: {
...mapState({
orders: 'orders',
clickOrderIndex: 'clickOrderIndex',
pageScrollTop: 'pageScrollTop'
}),
...mapGetters({
waitOrders: 'waitOrders',
waitOrdersNum: 'waitOrdersNum'
})
},
activated () {
document.querySelector('#vux_view_box_body').scrollTop = this.pageScrollTop
},
mounted () {
this.$nextTick(function () {
})
},
methods: {

showActionSheet (index, order) {
this.$store.commit('changeOrderMenu')
this.$store.commit('changeClickOrderIndex', {index})
},

toggleSidebar () {
console.log(this.sidebar)
this.sidebar.toggle()
}
}
}



